<template>
  <div class="bets" :class="{ active: isActive }">
    <p>{{ userHasMoney }}</p>
    <input type="number" v-model="betValue" />
    <button @click="bet(1)">홀</button>
    <button @click="bet(2)">짝</button>
    <p>{{ msg }}</p>
  </div>
  <div class="userRanked">
    <table class="table table-dark" style="margin-bottom: 0">
      <tr class="table-dark" style="">
        <td class="table-dark">아이디</td>
        <td class="table-dark">이름</td>
        <td class="table-dark">보유 금액</td>
        <td class="table-dark">가입일</td>
      </tr>
      <tr class="table-dark" v-for="user in users" :key="user">
        <td class="table-dark">{{ user.id }}</td>
        <td class="table-dark">{{ user.username }}</td>
        <td class="table-dark">
          {{ user.hasMoney }}
        </td>
        <td class="table-dark">
          {{ user.createdAt.split("T")[0] }}
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
import axios from "axios";

export default {
  name: "holzzakView",
  data() {
    return {
      msg: "",
      betValue: "",
      isActive: false,
      users: [],
    };
  },

  methods: {
    async bet(bettingValue) {
      this.active = true;
      // let betData = JSON.stringify({
      //   betValue: this.betValue,
      //   bettingValue: bettingValue,
      // });
      await axios
        .post(
          "/api/game/holzzak",
          {
            data: {
              betValue: this.betValue,
              bettingValue: bettingValue,
            },
          },
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem("jwt")}`,
            },
          }
        )
        .then((res) => {
          this.msg = res.data;
          this.active = false;
          this.$store.commit("updateUserMoney");
          this.getUsers();
        });
    },
    async getUsers() {
      await axios("/api/game/user", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("jwt")}`,
        },
      }).then((res) => {
        console.log(res.data);
        this.users = res.data;
      });
    },
  },
  computed: {
    userHasMoney() {
      return this.$store.getters.userInfo.hasMoney;
    },
  },
  watch: {
    users() {
      this.users = this.users.sort((a, b) => {
        return b.hasMoney - a.hasMoney;
      });
    },
  },
  async mounted() {
    this.getUsers();
    await this.$store.commit("updateUserMoney");
  },
};
</script>
<style>
.bets {
  margin-top: 5rem;
}
.userRanked {
  margin-top: 2rem;
}
@media (max-width: 575.98px) {
  .bets {
    margin-top: 7rem;
  }
}
</style>
